import web3 from '../utils/web3'
import routerAbi from '@/abi/pancakeRouter.json'
import config from '@/config/app.config.js' // 
const pancakeRouter = config.pancakeRouter
const routerContract = new web3.web3.eth.Contract(routerAbi, pancakeRouter)
// bscpancakeSwaprouter
// usdt
/**
 * tokenaddress---
 * @amountOut  unit256 1 
 * @path address[]
 * @returns amounts[amountin,amountout] unit256
 */
const getAmountsIn = async (
  tokenAddress,
  amountOut = 1,
  usdtAddress = config.USDT
) => {
  if (tokenAddress.toUpperCase() === usdtAddress.toUpperCase()) {
    return web3.successResult(amountOut)
  }
  amountOut = web3.toWei(amountOut)
  const path = [usdtAddress, tokenAddress]
  try {
    const resp = await routerContract.methods
      .getAmountsIn(amountOut, path)
      .call({})
    const amountIn = web3.fromWei(resp[0])
    return web3.successResult(amountIn)
  } catch (error) {
    return web3.failResult(error)
  }
}

const routerObj = {
  getAmountsIn
}
export default routerObj
