import request from '@/utils/request'

// 
export const handleMining = (params) => {
  return request('GET', '/dapp/mining/index', params, true, true, 1)
}

// 
export const handleExtractMining = (params) => {
  return request('GET', '/dapp/mining/extract', params)
}

// 
export const handleWarehourseList = (params) => {
  return request('GET', '/dapp/mining/list', params)
}
